<template>
    <!--=== blog page ===-->
    <section class="blog-posts">
        <div class="container">
            <div class="row">
                <div class="col">
                    <div class="row">
                        <div class="col-12">
                            <div class="section-header">
                                <div class="sec-icon"><i class="ion ion-android-list"></i></div>
                                <div class="sec-title">Honor</div>
                            </div>
                        </div>
                        <div class="col-lg-4 col-md-6 col-sm-6" v-for="(item,index) in pageList" :key="index">
                            <div class="blog-post">
                                <div class="post-img">
                                    <img class="img-fluid" :src="imgUrl + item.title_img" alt="image">
                                </div>
                                <div class="blog-details">
                                    <ul class="list-inline blog-item-links">
                                        <li class="list-inline-item"><a href="javascript:;"><i class=""></i>{{item.create_time | time}}</a></li>
                                    </ul>
                                    <a href="javascript:;"><h4 class="media-heading">{{item.title}}</h4></a>
                                    <p>{{item.content_descr}}</p>
                                    <a href="javascript:;" class="blog-post-link" @click="showMore(item)">More<i class="fa fa-angle-double-right"></i></a>
                                </div>
                            </div>
                        </div>
                        <div class="col-12">
                            <div class="see-more"><a href="javascript:;" class="btn btn-primary main-btn bg-main">show more</a></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
<!--        详情弹窗-->
        <el-dialog title="" :visible.sync="dialog">
            <div v-html="more.content"></div>
        </el-dialog>
    </section>
</template>

<script>
    export default {
        name: "blogpage",
