                }
            ],
            dialog:false,
            more:{}
        }),//data
        mounted(){
            this.getHonor();
        },
        methods:{
            getHonor(){
                let _self = this;
                _self.$ajax.post('/api/gethonor',{})
                    .then(res=>{
                        _self.pageList = res.resultData
                    })
            },
            showMore(item){
                this.more = item
                this.dialog = !this.dialog;
            }
        },
    }
</script>

